
'use server';

import { revalidatePath } from 'next/cache';
import admin from '@/lib/firebaseAdmin';

const aplicationRoles = {
  '00000001': 'admin',      // Magali - DNI sin puntos
  '00000002': 'peluquera',
  '00000003': 'transporte',
};

export async function completarPerfil(userId, userData) {
  const firestore = admin.firestore();
  const auth = admin.auth();

  const { dni, phone, direccion, localidad, provincia } = userData;
  
  if (!userId || !dni || !phone || !direccion || !localidad || !provincia) {
    return { success: false, error: 'Faltan datos por completar.' };
  }

  try {
    const userRole = aplicationRoles[dni] || 'cliente';

    await auth.setCustomUserClaims(userId, { role: userRole });

    await firestore.collection('users').doc(userId).set({
      dni,
      phone,
      direccion,
      localidad,
      provincia,
      role: userRole,
      profileCompleted: true,
    }, { merge: true });

    revalidatePath('/');
    
    return { success: true, role: userRole };

  } catch (error) {
    console.error('Error al completar el perfil:', error);
    return { success: false, error: 'Ocurrió un error en el servidor al procesar tu perfil.' };
  }
}

/**
 * Server Action para agregar una nueva mascota a un usuario.
 */
export async function agregarMascota(userId, mascotaData) {
    if (!userId) {
        throw new Error('Usuario no autenticado.');
    }

    const { nombre, especie, raza, fechaNacimiento } = mascotaData;

    if (!nombre || !especie || !raza || !fechaNacimiento) {
        throw new Error('Todos los campos son obligatorios.');
    }

    const firestore = admin.firestore();

    try {
        const mascotaRef = await firestore.collection('users').doc(userId).collection('mascotas').add({
            nombre,
            especie,
            raza,
            fechaNacimiento,
            enAdopcion: false, // Por defecto, una mascota no está en adopción
            createdAt: new Date(),
        });

        // Revalidamos la página de mascotas para que la nueva mascota aparezca inmediatamente.
        revalidatePath('/mascotas');

        return { success: true, mascotaId: mascotaRef.id };

    } catch (error) {
        console.error('Error al agregar la mascota:', error);
        throw new Error('No se pudo registrar la mascota en la base de datos.');
    }
}
